footer {
    position: relative;
    background-color: rgb(248,249,250);
    color: black;
 
  }

  .wave {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transform: scaleY(-1);
  }

  .footer-content {
    position: relative;
    z-index: 1;
    padding: 3%;
  }

  .ICONBUTTON:hover {
  
    color: white;
    transform: scale(1.1);
    /* background-color: white; */
}



.ICONBUTTON{
    transition: transform 0.2s;
    overflow: hidden;
    
    /* border: 1px solid black; */
}

  .HoverButton:hover {
    background-color: rgb(11, 0, 0);
    color: white;
    transform: scale(1.1);
}

.HoverButton{
    transition: transform 0.2s;
    overflow: hidden;
}