    @keyframes bounce {
        10% {
            height: 50px;
            width: 50px;
        }
        30% {
            height: 50px;
            width: 50px;
        }
        50% {
            height: 50px;
            width: 50px;
            transform: translateY(100px);
        }
        75% {
            height: 50px;
            width: 50px;
        }
        100% {
            transform: translateY(0px);
        }
    }

    .text-decoration-none{
        text-decoration: none;
    }

    * {
        margin: 0px;
        padding: 0px;
    }
    
    html{
        scroll-behavior: smooth;
        
    }
    
    
    .product{
        box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
        
    }
    
    .product:hover{
        box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
        
    }
    
    
    .text-decoration-none{
        text-decoration: none;
    }
    
    
    .GETBUTTON:hover {
      
        color: rgb(9, 0, 0);
        transform: scale(1.1);
        background-color: white;
    }
    
    
    
    .GETBUTTON{
        transition: transform 1.6s;
        overflow: hidden;
        
    }

    .CONTACTBUTTON:hover {
      
        color: rgb(9, 0, 0);
        transform: scale(1.1);
        background-color: #F1F1F1;
    }
    
    
    
    .CONTACTBUTTON{
        transition: transform 1.6s;
        overflow: hidden;
        
    }
    
    .arrow-btn {
        position: fixed;
        bottom: 30px;
        right: 15px;
        z-index: 1;
    }
    
    .arrow-btn a {
        height: 35px;
        width: 35px;
        text-align: center;
        background: rgb(2,115,186);
        display: block;
        border-radius: 3px;
        cursor: pointer;
    }
    
    .arrow-btn a span {
        color: #f2f2f2;
        line-height: 39px;
        font-size: 25px;
    }

